.contact {
    margin-bottom: var(--sidebar-margin);
    .title {
        @include sub-header;
    }
    .contact-item {
        display: flex;
        align-items: center;
        i {
            width: 16px;
            display: inline-block;
            // color: var(--fg-color);
        }
        a {
            text-decoration: none;
            padding-left: 4px;
            @include body-text;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}