.referees {
    margin-bottom: var(--sidebar-margin);
    .title {
        @include sub-header;
        margin: 0;
    }
    .referee {
        margin-bottom: 10px;
        .name {
            @include body-text;
            font-weight: bold;
        }
        .affiliation {
            @include body-text;
        }
        .email {
            @include body-text;
            font-size: 10pt;
            color: var(--link-color);
        }
    }
}