@mixin header {
    color: var(--fg-color-primary);
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 700;
    display: flex;
    flex-direction: row;
    &:before, &:after{
        content: "";
        flex: 1 1;
        border-bottom: 1px solid;
        margin: auto;
    }
    &:before {
        margin-right: 10px
    }
    &:after {
        margin-left: 10px
    }
}

@mixin sub-header {
    font-size: 18px;
    font-weight: 700; 
    color: var(--fg-color-primary);
}

@mixin body-text {
    color: var(--fg-color-body);
    font-size: 16px;
    line-height: 130%;
    margin-bottom: 0;
}

@mixin media {
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    .logo {
        margin: 0;
        padding: 0;
        img {
            width: 40px;
        }
    }
    .item {
        width: 100%;
        padding-left: 15px;
        .role {
            @include sub-header;
            .time {
                float: right;
                font-weight: normal;
                color: var(--fg-color-secondary);
            }
        }
        .organization {
            @include body-text;
            font-weight: bold;
            .location {
                font-style: italic;
                font-weight: normal;
            }
        }
        .details {
            @include body-text;
        }
    }
}