:root {
    --element-margin-bottom: 25px;
    --sidebar-margin: 20px;
}

* {
    font-family: 'Helvetica', 'Arial', sans-serif;
    text-align: justify;
    hyphens: auto;
    // line-height: 1.5;
}

body {
    background-color: var(--bg-color);
}

.container {
    max-width: 900px;
    min-height: 900px;
    margin: 0 auto;
    padding: 40px 60px;
    background-color: var(--bg-color-cv);
    box-shadow: 0px 2px 4px rgba(0,0,0,0.2);
    .container-block-sidebar-left {
        margin: 0;
        display: grid;
        grid-template-columns: 1fr 3fr;
        .main {
            margin-left: 20px;
        }
    }
    .container-block-sidebar-right {
        margin: 0;
        display: grid;
        grid-template-columns: 7fr 2fr;
        .main {
            margin-right: 50px;
        }
    }
}