.header {
    height: 140px;
    padding-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    .media {
        width: 100%;
        align-items: center;
        justify-items: center;
        display: grid;
        grid-template-columns: 3fr 1fr;
        img {
            width: 128px;
            border-radius: 50%;
        }
        .media-left {
            margin: 0;
        }
        .media-content {
            width: 100%;
            // flex-direction: column;
            // padding-left: 40px;
            .title {
                margin: 0;
                color: var(--fg-color-primary);
                font-weight: bolder;
                text-transform: uppercase;
            }
            .subtitle {
                margin: 0;
                font-weight: lighter;
                color: var(--fg-color-secondary);
            }
        }
        .media-right {
            margin: 0;
        }
    }
    input[type="checkbox"] {
        width: 0;
        height: 0;
        display: none;
    }
    label {
        cursor: pointer;
        color: var(--fg-color-secondary);
    }
}