@media print {
    @page {
        size: A4;
    }
    section {
        break-after: auto;
    }
    .container {
        margin:0;
        padding: 40px 60px;
        box-shadow: none;
        background-color: #ffffff;
        width: 1000px;
        .header {
            input, label {
                display: none;
            }
        }
    }
}